	padding: 20px;
}
.lodash__movies th {
	font-weight: 700;
	font-size: 32px;
	color: black;
	text-align: left;
	cursor: pointer;
}
.lodash__movies th span::after {
	position: relative;
	top: 0px;
	left: 15px;
	display: inline-block;
	visibility: hidden;
	width: 0px;
	height: 0px;
	overflow: hidden;
	margin-right: 10px;
	content: '';
	border: 10px;
	border-color: transparent;
	border-width: 10px;
	border-style: solid;
}
.lodash__movies th.is-active span::after {
	visibility: visible;
}
.lodash__movies th.sorted-asc span::after {
	top: 5px;
	border-top-color: deepskyblue;
}
.lodash__movies th.sorted-desc span::after {
	top: -5px;
	border-bottom-color: deepskyblue;
}
.lodash__movies th.is-active,
.lodash__movies th:hover {
	color: deepskyblue;
}
.lodash__movies td {
