}
.vuelidate__title {
	font-weight: 300;
	font-size: 32px;
	color: deepskyblue;
}
.vuelidate-grid {
	display: grid;
	grid-template-columns: 200px 1fr;
	grid-gap: 20px;
	max-width: 600px;
}
.vuelidate__label {
	font-weight: 300;
}
.vuelidate__label--input-field {
	padding-top: 15px;
}
.vuelidate__input {
	width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding: 15px;
	border-radius: 8px;
	border: 2px solid #ccc;
}
.vuelidate__label.is-valid {
	color: green;
}
.vuelidate__input.is-valid {
	border-color: green;
}
.vuelidate__label.has-error {
	color: red;
}
.vuelidate__input.has-error {
	border-color: red;
}
.vuelidate__error {
	display: block;
	margin-top: 10px;
	color: red;
	-ms-flex-item-align: center;
	    align-self: center;
}
.vuelidate__values {
	grid-column: 2;
}
.vuelidate__values .is-valid {
	color: green;
}
.vuelidate__values.do-shake {
	-webkit-animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
	animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
}
.vuelidate__newsletter-list {
	margin: 0;
	padding: 0;
	list-style: none;
}
.vuelidate__newsletter-list label {
	cursor: pointer;
}
.vuelidate__newsletter-list li {
	margin-bottom: 10px;
}
.vuelidate__values {
}
.vuelidate__button {
	margin-top: 20px;
	padding: 15px 30px;
	color: white;
	cursor: pointer;
	font-size: 26px;
	border: 0;
	border-radius: 8px;
	background-color: deeppink;
	-webkit-transition: background-color .3s;
	transition: background-color .3s;
	grid-column: 2;
	-ms-flex-item-align: center;
	    align-self: center;
}
.vuelidate__button[disabled] {
	color: #444;
	cursor: not-allowed;
	background-color: #ccc;
}
@-webkit-keyframes shake {
10%, 90% {
		-webkit-transform: translate3d(-1px, 0, 0);
		        transform: translate3d(-1px, 0, 0);
}
20%, 80% {
		-webkit-transform: translate3d(4px, 0, 0);
		        transform: translate3d(4px, 0, 0);
}
30%, 50%, 70% {
		-webkit-transform: translate3d(-8px, 0, 0);
		        transform: translate3d(-8px, 0, 0);
}
40%, 60% {
		-webkit-transform: translate3d(8px, 0, 0);
		        transform: translate3d(8px, 0, 0);
}
}
@keyframes shake {
10%, 90% {
		-webkit-transform: translate3d(-1px, 0, 0);
		        transform: translate3d(-1px, 0, 0);
}
20%, 80% {
		-webkit-transform: translate3d(4px, 0, 0);
		        transform: translate3d(4px, 0, 0);
}
30%, 50%, 70% {
		-webkit-transform: translate3d(-8px, 0, 0);
		        transform: translate3d(-8px, 0, 0);
}
40%, 60% {
		-webkit-transform: translate3d(8px, 0, 0);
		        transform: translate3d(8px, 0, 0);
}
}
